<template>
    <Nav></Nav>
    <div class='detail_grid'>
        <div class='detail_img'>
            <img :src="Data[id].image" alt="">
            <img v-for='i in Data.image' :key='i' :src='Data[id].image'>
        </div>
        <div class='detail_content'>
            <h3>{{Data[id].product_name}} {{Data[id].color}}</h3>


            <div class='detail_contents'>
                <p class='made'>제조사: {{Data[id].made}}</p>
                <p class='₩'>가격:₩ </p>
                <p class='price'>{{Data[id].reprice}}</p>
                <p class='sale_price'>{{Data[id].price}}</p>
                <p>배송비: ₩2,500</p>
            </div>
            <h3>색상 선택</h3>
            <select data-title='컬러' id='color' class='color_select' @change='abcd($event)'>
                <option value="0">색상</option>
                <option value="1">{{Data[id].color}}</option>
            </select>
            <h3>용량 선택</h3>
            <select data-title='용량' class='byte_select' @change='abc($event)'>
                <option value="0">용량</option>
                <option value="3">-----------------------</option>
                <option value="1">{{Data[id].byte}}</option>
            </select>
            <div class='total_price'>
                <h2>총 금액: {{TotalPrice}}원</h2>
                <h3>구매 확정</h3>
            </div>
        </div>
    </div>
</template>
    
<script>
import Nav from "../components/nav.vue";
import Low from '../product/low'

export default {
    data() {
        return {
            Data: Low,
            id: this.$route.params.id - 1,
            TotalPrice: '0',

        }
    },


    components: {
        Nav
    },
    methods: {
        abc(event) {
            const value = event.target.value

            if (value == 1) {
                this.TotalPrice = this.Data[this.id].price
            }
            if (value == 2) {
                this.TotalPrice = this.Data[this.id].price2

            }
            if (value == 3) {
                alert('용량을 선택해 주세요')
                event.target.value = '0'
            }
        },
    }
}
</script>
    
