import { createRouter, createWebHistory } from 'vue-router';
import Home from "../view/Home.vue";
import Apple from "../view/Apple.vue";
import Galaxy from "../view/Galaxy.vue";
import Hudo from "../view/Hudo.vue";
import Low from "../view/Low.vue";
import Signin from "../view/Signin.vue";
import Signup from "../view/Signup.vue";
import IphDetail from "../view/IphDetail.vue";
import GalDetail from "../view/GalDetail.vue";
import HdDetail from "../view/HdDetail.vue";
import LowDetail from "../view/LowDetail.vue";

const routes = [
    {
        path: "/",
        name: "Home",
        component: Home,
    },
    {
        path: "/iph",
        name: "Apple",
        component: Apple,
        props: true,
    }, {
        path: "/ssph",
        name: "Galaxy",
        component: Galaxy,
        props: true,
    },
    {
        path: "/fdph",
        name: "Hudo",
        component: Hudo,
        props: true,
    }, {
        path: "/lowph",
        name: "Low",
        component: Low,
        props: true,
    },
    {
        path: "/signin",
        name: "Signin",
        component: Signin,
    },
    {
        path: "/signup",
        name: "Signup",
        component: Signup,
    },
    {
        path: "/iph/:id",
        name: "IphDetail",
        component: IphDetail,
        props: true,
    }, {
        path: "/ssph/:id",
        name: "GalDetail",
        component: GalDetail,
        props: true,
    }, {
        path: "/fdph/:id",
        name: "HdDetail",
        component: HdDetail,
        props: true,
    }, {
        path: "/lowph/:id",
        name: "LowDetail",
        component: LowDetail,
        props: true,
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router